<template lang="html">
    <div class="">
        <v-container grid-list-md text-xs-center>
            <h1 @click="home">Login</h1>
            <v-layout row wrap justify-center>
                <v-flex xs4 >
                    <v-text-field
                      name="name"
                      label="Username"
                      v-model="email"
                    ></v-text-field>
                </v-flex>
                <v-flex xs4 >
                    <v-text-field
                        name="name"
                        label="Password"
                        v-model="password"
                        :type="'password'"
                    ></v-text-field>
                </v-flex>
                <v-flex xs12>
                    <v-btn color="primary" @click="handleSumbit">Sign In</v-btn>
                </v-flex>
                <v-flex xs12>
                    <v-btn color="primary" @click="Test">Test</v-btn>
                </v-flex>
            </v-layout>
        </v-container>

    </div>
</template>

<script>
import { mapGetters, mapActions} from "vuex"
//import ApiService from "../../../services/api.service"

export default {
    name: "login",

    data(){
        return {
            email:"",
            password:""
        }
    },

    computed: {
        ...mapGetters("auth",[
            "authenticating",
            "authenticationError",
            "authenticationErrorCode"
        ])
    },

    methods: {
        ...mapActions("auth",[
            "login"
        ]),

        handleSumbit(){
            if (this.email !="" && this.password != "") {
                this.login({email: this.email, password: this.password})
                this.password = ""
            }
        },

        home(){
            this.$router.push("/")
        },

        handleSumbitTest(){
            alert(this.email + " "+ this.password)
        },
        Test(){
            alert("Test")
        }
    }
}
</script>

