const TOKEN_KEY = 'access_token'
const REFRESH_TOKEN_KEY = 'refresh_token'
const EXPIRE_TIME_KEY = "expires"

/*
 * Manage the how Access Tokens are being stored and retreive from storage.
 * Current implementation stores to localStorage, Local Storage should always be access through this instance
 */

 const TokenService = {

    getToken(){
        if(Date.now() > this.getExpireTime()){
            return ""
        }
        return localStorage.getItem(TOKEN_KEY)
    },

    saveToken(accessToken){
        localStorage.setItem(TOKEN_KEY, accessToken)
    },

    removeItem(){
        localStorage.removeItem(TOKEN_KEY)
    },

    getRefreshToken(){
        return localStorage.getItem(REFRESH_TOKEN_KEY)
    },

    saveRefreshToken(refreshToken){
        localStorage.setItem(REFRESH_TOKEN_KEY, refreshToken)
    },

    removeRefreshToken(){
        localStorage.removeItem(REFRESH_TOKEN_KEY)
    },

    getExpireTime(){
        return localStorage.getItem(EXPIRE_TIME_KEY)
    },

    saveExpireTime(expireTime){
        localStorage.setItem(EXPIRE_TIME_KEY, Date.now() + expireTime)
    },

    removeExpireTime(){
        localStorage.removeItem(EXPIRE_TIME_KEY)
    }
 }

export { TokenService }
