import ApiService from "./api.service"
import { TokenService } from "./storage.service"

class AuthenticationError extends Error {
    constructor(errorCode, message){
        super(message)
        this.name = this.constructor.name
        this.message = message
        this.errorCode = errorCode
    }
}

const UserService = {
    /**
     * Login the user and store access token to TokenService.
     *
     * @return access_token
     * @throws AuthenticationError
    **/
    login: async function(email, password){
        const requestData = {
            method: "post",
            url: "/login",
            data:{
                username: email,
                password: password
            }
        }
        
        try {
            const response = await ApiService.customeRequest(requestData)

            TokenService.saveToken(response.data["data"]["access_token"])
            TokenService.saveRefreshToken(response.data["data"]["refresh_token"])
            TokenService.saveExpireTime(response.data["data"]["expires"])
            ApiService.setHeader()


            //ApiService.mount401Interceptor();
            return response.data["data"]["access_token"]
        } catch(error){
            throw new AuthenticationError(error.response.status, error.response.data.details)
        }
    },

    /*
     * Refresh the access token.
     */
    refreshToken: async function(){
        const refreshToken = TokenService.getRefreshToken()

        const requestData = {
            method: "post",
            url: "/refresh-token",
            data: {
                refreshToken: refreshToken
            }
        }

        try {
            const response = await ApiService.customeRequest(requestData)

            TokenService.saveToken(response.data["data"]["access_token"])
            // Update the header with the new token
            ApiService.setHeader()

            return response.data["data"]["access_token"]
        } catch(error){
            throw new AuthenticationError(error.response.status, error.response.data.detail)
        }
    },

    /*
     * Logout the current user by removing the token from storage.
     *
     * Will also remove the Authorization header from the ApiService as well
     */
    logout() {
        TokenService.removeToken()
        TokenService.removeRefreshToken()
        TokenService.removeExpireTime()
        ApiService.removeHeader()

        //ApiService.unmount401Interceptor()
    }
}


export default UserService

export { UserService, AuthenticationError }
