import Vue from "vue"
import "material-design-icons-iconfont/dist/material-design-icons.css"
import "./css/main.css"
import "./plugins/vuetify"
import router from "./router"
import store from "./store"
import "vuetify/dist/vuetify.min.css"
import App from "./App.vue"
import { TokenService } from "./services/storage.service"
import ApiService from "./services/api.service"

/*
 Check if we using the npm dev server or if we building it to serve it with our backend application
 So if the NODE_ENV environment is production then collect the host and protocol from the dom
 if not set it to localhost and port 5000
 */
var url = process.env.NODE_ENV === "production" ? "/api/v1/" : location.protocol + "//localhost:5000/api/v1/"
// Set the base url
ApiService.init(url)
//ApiService.init(process.env.VUE_APP_ROOT_API)
console.log("[*] Base Url:", url)

// If we have auth token set header
if (TokenService.getToken) {
    ApiService.setHeader()
}


Vue.config.productionTip = false


new Vue({
    router,
    store,
    render: h => h(App),
    }).$mount("#app")
