import Vue from "vue"
import Router from "vue-router"
import View from "@/components/views/View"
import Home from "@/components/views/Home.vue"
import Login from "@/components/views/Auth/Login.vue"
import AdminView from "@/components/views/Admin/AdminView"
import { TokenService } from "../services/storage.service"

Vue.use(Router)

const router = new Router({
    mode: "history",
    base: process.env.BASE_URL,

    routes: [
        {
            path:"/",
            component: View,
            children: [
                {
                    path:"/",
                    name: "Home",
                    component: Home,
                    meta: {
                        public: true
                    }
                },

            ]
        },
        {
            path: "/admin",
            name: "AdminView",
            component: AdminView,
            children: [
                {
                    path: "/about",
                    name: "About",
                    component: () => import(/* webpackChunkName: "about" */"@/components/views/About.vue")
                },
                {
                    path: "/tab1",
                    name: "Tab1",
                    component: () => import(/* webpackChunkName: "about" */"@/components/views/Admin/Tab1.vue")
                },
                {
                    path: "/tab2",
                    name: "Tab2",
                    component: () => import(/* webpackChunkName: "about" */"@/components/views/Admin/Tab2.vue")
                }
            ]
        },
        {
            path: "/login",
            name: "Login",
            component: Login,
            meta: {
                public: true, // Allow access to event if not logged in
                onlyWhenLoggedOut: true
            }
        },

    ]
})

router.beforeEach((to, from, next) => {
    const isPublic = to.matched.some(record => record.meta.public)
    const onlyWhenLoggedOut = to.matched.some(record => record.meta.onlyWhenLoggedOut)
    const loggedIn = !!TokenService.getToken()


    if (!isPublic && !loggedIn) {
        return next({
            path:"/login",
            query:{redirect: to.fullPath} // Store the full path to redirect the user to after loggin
        })
    }

    // Do not allow user to visit login page or register page if they are logged in
    if (loggedIn && onlyWhenLoggedOut) {
        return next("/")
    }

    next()
})
export default router;
